.button {
    @apply inline-block font-light rounded-2xl py-1.5 px-5 cursor-pointer;

    &--primary {
        @apply bg-green-600 text-white;
    }

    &--secondary {
        @apply bg-gray-500 text-white;
    }

    &--danger {
        @apply bg-red-600 text-white;
    }

    &--disabled {
        @apply bg-opacity-25 pointer-events-none cursor-default;
    }
}
.image-preview {
  @apply w-full;
  &__thumbnail {
    width: 50px;
    height: 50px;
  }

  &__image-name {
    @apply text-sm;
  }
}
@tailwind base;
@tailwind components;
@tailwind utilities;
@import "src/styles/components/_button.scss";
@import "src/styles/components/_image-preview.scss";


